@import "../../../global.scss" ;


.home{

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  
  >.wrapper{
    background-color: $color2;
    width: 100%;
    padding: 10px;
    display: flex; 
    flex-direction: row;
    flex-wrap: wrap;

    >h1{
      padding:10px;
      font-family: sans-serif;
      flex:100%;
      width:100%;
    }
    >h3{
      width:100%;
      text-align: center;
    }

    >.filter-con{
      flex:20%;

    }

    >.info-con{
      flex:80%;
    }

  }
}





@media screen and (max-width:$breakpoint2) {
  .home{
    >.wrapper{
      >.filter-con{
        flex:100%;
        
      }
      >.info-con{
        flex:100%;
      }
    }
  }
}

@media screen and (max-width:$breakpoint1) {
  .home{
    >.wrapper{
      >.filter-con{
        flex:100%;
      }
      >.info-con{
        flex:100%;
      }
    }
  }
}

