@import "../../../global.scss";


.filter{

  >.wrapper{
    background-color: $color1;
    border-radius: 10px;
    padding-top: 1rem;

    >.filter2{

      >.heading{
        font-size: x-large;
        font-weight: bold;
        font-family: sans-serif;
        margin: 2rem ;
      }

      .years{

       
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        padding: 5px;
        padding-bottom: 15px;

      >.heading{
          width: 100%;
          text-align: center;
          padding:5px 10px;
          text-decoration: underline;
          font-size: large;
        }
        

        .each-year{
          flex:50%;
          border-radius: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          padding:5px;
          

          span{
            min-width:60%;
            text-align: center;
            background-color: $color5;
            padding:10px 20px;
            border-radius: 8px;
            cursor: pointer;
            transition: $trans1;
            &.active{
              background-color: $color4;
            }
          }

        }
      }
      .launch, .land{
        padding:15px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;

        .heading{
          flex: 100%;
          padding:10px;
          text-align: center;
          text-decoration: underline;
          font-size: large;
        }
        .option{
          flex:50%;
          display: flex;
          align-items: center;
          justify-content: center;

          span{
            background-color: $color5;
            padding: 10px 20px ;
            border-radius: 8px;
            cursor: pointer;
            transition: $trans1;
            min-width: 50%;
            text-align: center;

            &.active{
              background-color: $color4;
            }

          }
        }
        

      }

    }
  }
}