@import "../../../global.scss" ;

.info{

  >.wrapper{

    >.card-con{
      padding:10px;
      border-radius:  8px;
      display: grid;
      grid-template-columns: 25% 25% 25% 25%;
      grid-auto-rows: 1fr;


      .card{
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;


        .each-card{
          background-color: $color1;
          max-width: 100%;
          width: 100%;
          height: 100%;

          
          border-radius: 5px;

          padding:10px;

          .heading{
            color: $color6 ;
            font-size: 1rem;
            padding: 5px;
          }

          >.img{
            width:100%;
            background-color: $color2;
            padding: 3px ;

            img{
              max-width: 100%;
              width:100%;
            }
          }
          >.info{
            >p{
              padding:5px;

              >span{
                color:$color6;
              }
            }
          }
        }
      }
    }
  }
}




@media screen and (max-width:$breakpoint2) {
  .info{

    >.wrapper{

      >.card-con{
        grid-template-columns: 50% 50%;
      }
    }
  }
}


@media screen and (max-width:$breakpoint1) {
  .info{

    >.wrapper{

      >.card-con{
        grid-template-columns: 100%;

        >.card{

          >.each-card{
            padding:20px;
            
          }
        }
      }
    }
  }
}